import { brandArr, typesArr } from '../../mock/index/data.js';
import goodsList from '../components/goods-list.vue';
export default {
	components: {
		goodsList
	},
	data() {
		return {
			baseUrl: 'http://39.108.65.247:8087',
			brandArr,
			typesArr,
			typeSelindex: 1,
			brandSelIndex: [],
			isShowVeh: true,
			tabsIndex: 0,
			tabsArr: [
				{
					text: '全部',
					tag: 'all',
					icon: ''
				},
				{
					text: '品牌',
					tag: 'brand',
					icon: 'http://39.108.65.247:8087/imgs/class/brand-icon.png'
				},
				{
					text: '促销',
					tag: 'sale',
					icon: 'http://39.108.65.247:8087/imgs/class/sale-icon.png'
				}
			]
		}
	},
	methods: {
		tabsSwitch (num) {
			if (num === 2) {
				uni.navigateTo({
				    url: "/pages/class/sale"
				});
			} else {
				this.tabsIndex = num;
			}
		},
		clickBrand (index) {
			this.brandSelIndex.push(index);
		},
		resetDrawer() {
			this.brandSelIndex = [];
		},
		submitDrawer() {
			this.tabsIndex = 0;
		},
		typeSelScroll (index) {
			this.typeSelindex = index
		},
		skipTo () {
			uni.navigateTo({
			    url: "/pages/mine/new-goods"
			});
		},
		skipToVehInfo() {
			uni.navigateTo({
			    url: "/pages/other/veh-detail"
			});
		}
	}
}
